import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-play',
  templateUrl: './play.component.html',
  styleUrls: ['./play.component.css']
})
export class PlayComponent implements OnInit {
  myData: any;
  w: any;
  h: any;
  bg: any;
  margin: any;
  padding: any;
  borderradius: any;
  border: any;
  outline: any;
  newValue: string;
  names: string[] = ['ahmed', 'mostafa', 'mohamed', 'anwar'];
  addName() {
    this.names.push(this.newValue)
    this.newValue = "";
  }

  removeNames(data: string) {
    let index = this.names.indexOf(data);
    this.names.splice(index, 1);
  }


  constructor() { }

  ngOnInit(): void {
  }

}
